	import {uniCollapse,uniCollapseItem} from "@dcloudio/uni-ui"
	export default {
	
		data() {
			return {
				array: [],
				array1:[],
                index: "",
				selectValue:"", 
				imageList:[],
				Id:"",
				pathArr:[],
				listPath:[],
				name:"",
				tel:"",
				percent:0,
				showBox:false,
				state:false
				
			}
		},
		onLoad:function(){
			var _self = this
			_self.getComPonyInfo()
		},
		methods: {
			/**
			 * 删除数组图片
			 */
			deletePic:function(e){
				var _self = this
				console.log(e)
				_self.imageList.splice(e,1)
				_self.pathArr.splice(e,1)
			},
			bindPickerChange: function(e) {
            console.log('picker发送选择改变，携带值为', e.target.value)
            this.index = e.target.value
			this.Id = this.array1[this.index]
			console.log(this.Id)
			},
			previewImage: function(e) {
				var current = e.target.dataset.src
				uni.previewImage({
					current: current,
					urls: this.imageList
				})
			},
			chooseImage: async function() {
				var _self = this
					if (this.imageList.length === 9) {
						let isContinue = await this.isFullImg();
						console.log("是否继续?", isContinue);
						if (!isContinue) {  
							return;
						}
					}
			uni.chooseImage({
					success: (res) => {
						console.log(res)
						console.log(JSON.stringify(res.tempFilePaths));
						var tempFilePaths = res.tempFilePaths;
						//console.log(JSON.stringify(res.tempFilePaths));
						this.imageList = this.imageList.concat(res.tempFilePaths);
						console.log(this.imageList)
						console.log(tempFilePaths)
						for(var i =0;i<tempFilePaths.length;i++){
						uni.uploadFile({
							url: _self.$api+'dockingManager/upload', //仅为示例，非真实的接口地址
							//url: "http://www.tp5.com/index", //仅为示例，非真实的接口地址
							filePath: tempFilePaths[i],
							name: 'file', 
							formData: {
								'user': 'test'
							}, 
							success: (uploadFileRes) => {
								_self.pathArr = _self.pathArr.concat(JSON.parse(uploadFileRes.data)) 
								_self.state = true
								console.log(_self.pathArr)   
								console.log(uploadFileRes);
							}
						}); 
						}
						//  uploadTask.onProgressUpdate((res) => {
						// 	_self.showBox = true
						// 	_self.percent = res.progress
						// });
					}
				})
			},
			showOrHide:function(){
					this.showBox = false
				},
				isFullImg: function() {
					return new Promise((res) => {
						uni.showModal({
							content: "已经有9张图片了,是否清空现有图片？",
							success: (e) => {
								if (e.confirm) {
									this.imageList = [];
									res(true);
								} else {
									res(false)
								}
							},
							fail: () => {
								res(false)
							}
						})
					})
				},
			getComPonyInfo:function(){
				var _self = this
				uni.request({
					url:_self.$api+"dockingManager/cardLower",
					success:function(res){
						var list = []
						var list1 = []
						
						for(var i =0;i<res.data.length;i++){
							 list[i]  = res.data[i].trade
							 list1[i] = res.data[i].id
						}
						_self.array = list
						_self.array1 = list1 
						console.log(res)
						console.log(list)
						
					}
				})
			},
			addInfo:function(){
				var _self = this
				var myreg = /^((0\d{2,3}-\d{7,8})|(1[3456789]\d{9}))$/;
				if(_self.index === ""){
					uni.showToast({
						title:"请选择加入的企业",
						icon:"none"
					})
					return false
				}else if(_self.name == ""){
					uni.showToast({
						title:"姓名不能为空",
						icon:"none"
					})
					return false
				}
				else if(_self.tel == ""){
					uni.showToast({ 
						title:"手机号不能为空",
						icon:"none"
					})
					return false
				}
				else if(!myreg.test(_self.tel)){
						uni.showToast({
						title:"联系电话格式不正确",
						icon:"none"
					})
					return false
				}
				// else if(_self.percent != 100 && _self.imageList.length>0){
				// 	uni.showToast({
				// 		title:"图片正在上传，请稍后提交",
				// 		icon:"none"
				// 	})
				// 	return false
				// }
				else if(_self.state == false && _self.imageList.length>0){ 
					uni.showToast({
						title:"图片正在上传，请稍后提交",
						icon:"none"  
					})
					return false
				}
				for(var i =0;i<_self.pathArr.length;i++){
					_self.listPath.push({testName1:_self.pathArr[i]})
					console.log(_self.listPath)
				}
				console.log(_self.Id)
				_self.listPath.push({name:_self.name,phone:_self.tel,optionId:uni.getStorageSync("openId"),store:1,upperId:_self.Id})
				console.log(_self.listPath)
					uni.request({
					url:_self.$api+"dockingManager/cardAdd",
					data:JSON.stringify(_self.listPath),
					method:"POST",
					success:function(res){
						console.log(res)
						if(res.data == 1){
							uni.showToast({ 
								title:"提交成功",
								duration:2000,
								success:function(){
								uni.setStorageSync("componyOwner",5)   
										setTimeout(function(){
										uni.switchTab({
											url:"../../../pages/mine/mine"
										})
									},1000) 
								}
							})
							return false
						}
						else if(res.data == 99){
							uni.showToast({
								title:"您尚未进行登录，请登录后重试",
								icon:"none"
							})
							return false
						}
					}
					})
			},
		},
		 components: {uniCollapse,uniCollapseItem}
	}
